import React from "react"
import loadable from '@loadable/component'
import * as Constants from "../constants/contants"
import { Helmet } from "react-helmet"
import { StaticQuery, graphql } from "gatsby"
const Layout = loadable(() => import('../components/layout'))
const Main = loadable(() => import('../components/main'))


export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {content: Constants.home };
    this.changeComponent = this.changeComponent.bind(this);
    
  }

  changeComponent(sTab){
    this.setState({
      content: sTab
    });
  }

  render(){    
    return (
      
        <StaticQuery
          query={graphql`
            query {
              site {
                siteMetadata {
                  title
                  description
                  author
                }
              }
            }
          `}
          render={data => 
            <>
            <Helmet 
                htmlAttributes={{ lang : 'en'}}
                meta={[
                {
                  name: `description`,
                  content: data.site.siteMetadata.description,
                },
                {
                  property: `og:title`,
                  content: data.site.siteMetadata.title,
                },
                {
                  property: `og:description`,
                  content: data.site.siteMetadata.description,
                },
                {
                  property: `og:type`,
                  content: `website`,
                },
                {
                  name: `twitter:card`,
                  content: `summary`,
                },
                {
                  name: `twitter:creator`,
                  content: data.site.siteMetadata.author,
                },
                {
                  name: `twitter:title`,
                  content: data.site.siteMetadata.title,
                },
                {
                  name: `twitter:description`,
                  content: data.site.siteMetadata.description,
                },
              ]}>                
                <title>Luis Inga Rivera</title> 
                {/* Global site tag (gtag.js) - Google Analytics */}
                <script async src="https://www.googletagmanager.com/gtag/js?id=G-12PJHL04XQ"></script>
                <script>
                  {`window.dataLayer = window.dataLayer || [];
                  function gtag(){dataLayer.push(arguments);}
                  gtag('js', new Date());
                  if (navigator.sendBeacon){
                    gtag('config', 'G-12PJHL04XQ',{ 'transport_type': 'beacon'});
                  }else{
                    gtag('config', 'G-12PJHL04XQ');
                  }                  
                  `}
                </script>              
              </Helmet>
              <Layout changeComponent={this.changeComponent}>
                <Main tab={this.state.content}>          
                </Main>
              </Layout>
              </>
          }
        />
        
      
    )
  }
}
